const Note = ({note, toggleImportanceOf})=>{ 
    const buttonLabel = note.important ? "Make note Important" : "Make Importnant"
    return(
        <div>
            <li className="note">{note.content}</li>
            <button onClick={() => toggleImportanceOf(note.id)}>{buttonLabel}</button>
        </div>
        
    )
}




export default Note