import { useEffect, useState } from 'react'
import Note from './components/note'
import NotesServices from './services/notes'

const App = () => {
  const [notes, setNotes] = useState([])
  const [newNote, setNewNotes] = useState("a new note...")
  const [showAll, setShowAll] = useState(true)


  const hook = () => {
    NotesServices
      .getAll()
      .then(res => {
        setNotes(res)
      })
  }

  useEffect(hook, [])


  const addNote = (e) => {
    e.preventDefault()
    const newObj = {
      content: newNote,
      date: new Date().toString(),
      important: Math.random() < 0.5
    }

    NotesServices
      .create(newObj)
      .then(res => {
        setNotes(notes.concat(res))
        setNewNotes('')
      })
  }

  const handleSetNewNotes = e => {
    setNewNotes(e.target.value)
  }

  const toggleImportanceOf = id => {
    const note = notes.find(n => n.id === id)
    const changedNote = { ...note, important: !note.important }

    NotesServices
      .update(id, changedNote)
      .then(res => {
        setNotes(notes.map(note => note.id !== id ? note : res))
      })
  }

  const notesToShow = showAll ? notes : notes.filter(note => note.important === true)

  return (
    <div>
      <h1>Notes</h1>
      <button onClick={() => setShowAll(!showAll)}>
        {showAll ? 'Show Important' : "Show All"}
      </button>
      <ul>
        {notes === '' ? "Loading..." : notesToShow.map(note =>
          <Note key={note.id} note={note} toggleImportanceOf={toggleImportanceOf} />
        )}
      </ul>
      <form onSubmit={addNote}>
        <input type="text" value={newNote} onChange={handleSetNewNotes} />
        <button type="submit">Save</button>
      </form>
    </div>
  )
}

export default App